$icons: (0:0)
$icons: map-merge($icons,(arrow: (X: -39px, Y:-98px, W: 8px, H: 9px, TW: 215px, TH: 127px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(first-tab-active: (X: 0px, Y:0px, W: 215px, H: 39px, TW: 215px, TH: 127px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(first-tab: (X: 0px, Y:-49px, W: 215px, H: 39px, TW: 215px, TH: 127px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(search: (X: 0px, Y:-98px, W: 29px, H: 29px, TW: 215px, TH: 127px, IMG: '../img/icons.png')))


// Gets an attribute from the sass map
@function icon-attr($icon, $attr)
	$icon: map-get($icons, $icon)
	@return map-get($icon, $attr)

@mixin sprite($iconName)
	background-image: url(icon-attr($iconName, IMG))
	width: icon-attr($iconName, W)
	height: icon-attr($iconName, H)
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y)
@mixin sprite-position($iconName)
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y)
@mixin sprite-retina($iconName)
	background-image: url(icon-attr($iconName, IMG))
	$width: icon-attr($iconName, W)
	$height: icon-attr($iconName, H)
	width: $width/2
	height: $height/2
	$x: icon-attr($iconName, X)
	$y: icon-attr($iconName, Y)
	background-position: $x/2 $y/2
	$tw: icon-attr($iconName, TW)
	$th: icon-attr($iconName, TH)
	background-size: $tw/2 $th/2


@mixin s($i)
	+sprite($i)
@mixin sp($i)
	+sprite-position($i)
@mixin sr($i)
	+sprite-retina($i)

.icon-arrow
	width: 8px
	height: 9px
	background-image: url('../img/icons.png')
	background-position: -39px -98px
.icon-first-tab-active
	width: 215px
	height: 39px
	background-image: url('../img/icons.png')
	background-position: 0px 0px
.icon-first-tab
	width: 215px
	height: 39px
	background-image: url('../img/icons.png')
	background-position: 0px -49px
.icon-search
	width: 29px
	height: 29px
	background-image: url('../img/icons.png')
	background-position: 0px -98px
