$base-color:                             #303134;
$light:                                  #fff;
$dark:                                   #000;
$light-grey:                             #ededed;
$blue-base:                              #0b2d77;
$blue-base-light:                        #1e99c3;
$nav-bg:                                 #61afd0;
$nav-bg-active:                          #fff;
$select-border:                          #a9adb2;
$select-bg:                              #fefefe;
$select-bg-grad:                         #eaeaea;
$input-top-border:                       #aaadb2;
$input-side-border:                      #e2e2ec;
$input-bottom-border:                    #e2e9ef;
$btn-box-shadow:                         rgba(0, 0, 0, .125);

/**
 * Helper
 */
.clearfix:after {
	display: block;
	height: 0;
	clear: both;
	font-size: 0;
	content: ' ';
	visibility: hidden;
}

* html .clearfix { zoom: 1; } /* IE6 */
*:first-child+html .clearfix { zoom: 1; } /* IE7 */

/**
 * Base
 */
body {
	padding: 15px;
	color: $base-color;
	font-family: 'Arial', sans-serif;
	font-size: 12px;
	font-weight: 400;
	line-height: 2;
}

input,
textarea { color: $base-color; }

.placeholder { color: $base-color; }

::placeholder {
	color: $base-color;
	opacity: 1;
}

/**
 * BLOCK b-search
 */
.b-search {
	width: 436px;
	margin: 0 auto;
	padding: 16px 7px 8px 6px;
	background: $blue-base;
	background: linear-gradient(0deg, $blue-base 0%, $blue-base-light 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#1e99c3', endColorstr='#0b2d77', GradientType=0);

	&__head {
		margin-bottom: 9px;
		padding: 0 18px;
	}

	&__title {
		color: $light;
		font-size: 18px;
		font-weight: normal;
		line-height: 1.1;
		text-transform: uppercase;
	}

	&__icon {
		@include s(search);
		display: inline-block;
		margin-right: 15px;
		vertical-align: middle;
	}
}

/**
 * BLOCK tab
 */
.tab {
	&__content {
		background: $light;
	}

	&__pane {
		display: none;
		padding: 21px 16px;

		&.is-active {
			display: block;
		}
	}

}

/**
 * BLOCK nav
 */
.nav {
	display: block;

	&__item {
		width: 215px;
		float: left;
		font-size: 12px;

		+ .nav__item {
			margin-left: 6px;

			.nav__link {
				background: $nav-bg;
			}

			.nav__link:hover,
			.nav__link:focus,
			.nav__link:active,
			.is-active {
				background: $light;
				color: $blue-base-light;
			}
		}

		.nav__link:hover,
		.nav__link:focus,
		.nav__link:active,
		.is-active {
			@include sprite-position(first-tab-active);
			color: $blue-base-light;
		}
	}

	&__link {
		display: block;
		padding: 10px 14px 5px;
		background: url('../img/icons.png');
		@include sprite-position(first-tab);
		color: $light;
		text-align: center;
		text-decoration: none;
		text-transform: uppercase;
	}


}

/**
 * BLOCK form
 */
.form {
	&__foot {
		padding: 21px 30px 17px 64px;
		background: $light url('../img/form-foot-bg.png') center top no-repeat;
	}

	&__result-text {
		margin-bottom: 7px;
		color: $blue-base;
		font-size: 14px;
		line-height: 1.286;
	}

	&__body {
		margin-bottom: 7px;
	}
}
/**
 * Modificator .form_tab-search
 */

.form_tab-search {
	&__map {
		width: 156px;
		margin-right: 22px;
		padding: 17px 18px 3px 0;
		float: left;
		border-right: 1px solid $light-grey;
	}

	&__fields {
		width: 207px;
		float: left;
	}
}

.form-group {
	margin-bottom: 26px;
	line-height: 1;
}

.form-group_oneline {

	.form-field,
	.form-select {
		display: inline-block;
		width: 120px;
		margin-left: 19px;
	}
}

.form-hint {
	padding: 9px 0 7px;
	color: $blue-base;
	font-size: 11px;
}

label,
.label {
	display: inline-block;
	margin-bottom: 7px;
	outline: 0;
	color: $blue-base;
}

.form-field,
.form-select {
	display: block;
	width: 100%;
	height: 23px;
	padding: 2px 3px 3px;
	border: 1px solid $input-side-border;
	border-radius: 2px;
	border-top-color: $input-top-border;
	border-bottom-color: $input-bottom-border;
	background: $light;
	color: $base-color;
	font-size: 11px;
	line-height: 1;
	box-sizing: border-box;
}

.form-select {
	height: 23px;
	border-top-left-radius: 0;
	border-color: $select-border;
	background: $select-bg-grad;
	background-image: url('../img/select-btn.png'), linear-gradient(0deg, $select-bg-grad 0%, $light 100%);
	background-repeat: no-repeat;
	background-position: 96% center;
	appearance: none;
	box-sizing: border-box;
}

.c-box-group {
	line-height: 1;
}

select::-ms-expand {
	display: none;
}

.c-box {
	position: relative;
	margin: 0 8px 0 0;
	padding: 3px 0 0;
	cursor: pointer;

	input {
		position: absolute;
		margin: 0;
		clip: rect(0, 0, 0, 0);
		vertical-align: middle;
		visibility: hidden;

		&:checked + .c-box__custom:after {
			display: block;
		}
	}
}

.c-box__custom {
	display: inline-block;
	position: relative;
	top: -2px;
	width: 10px;
	height: 10px;
	margin: 0 4px 0 0;
	border: 1px solid $select-border;
	background: $dark;
	background: linear-gradient(0deg, $select-bg-grad 0%, $light 100%);
	vertical-align: middle;

	&:after {
		display: none;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		width: 4px;
		height: 4px;
		margin: auto;
		border-radius: 50%;
		background: $dark;
		content: '';
	}
}

.btn {
	height: 34px;
	padding: 8px 19px 10px;
	border: 0;
	border-radius: 2px;
	background: $blue-base;
	background: linear-gradient(0deg, $blue-base 0%, $blue-base-light 100%);
	color: $light;
	font-size: 14px;
	font-weight: bold;
	line-height: 1.2;
	text-transform: uppercase;
	cursor: pointer;
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#1e99c3', endColorstr='#0b2d77', GradientType=0);

	&__right-icon {
		display: inline-block;
		margin-left: 18px;
		vertical-align: middle;
	}

	&:hover,
	&:focus {
		background: linear-gradient(0deg, $blue-base 0%, $blue-base-light 85%, $blue-base-light 100%);
	}

	&:active {
		padding: 9px 19px;
		box-shadow: inset 0 3px 5px $btn-box-shadow;
	}
}
